import React from "react";
import "./Navbar.css"

const Navbar = () => (
  <div className="container-fluid text-center">
    <div className="row">
      <div id="navbar" className="col">
        <h1>NYT Article Scrubber</h1>
      </div>
    </div>
  </div>
);

export default Navbar;



